import proxy from './proxy'

const getPage = (params) => {
  return proxy({
    url: '/user/page',
    method: 'get',
    params
  })
}

const create = (user) => {
  return proxy({
    url: '/user/create',
    method: 'post',
    data: user
  })
}

const remove = (names) => {
  return proxy({
    url: '/user/delete',
    method: 'post',
    data: names
  })
}

const update = (user) => {
  return proxy({
    url: '/user/update',
    method: 'post',
    data: user
  })
}

export default { getPage, create, remove, update }



