import proxy from './proxy'

const create = (menu) => {
  return proxy({
    url: 'menu/create',
    method: 'post',
    data: menu
  })
}

const update = (menu) => {
  return proxy({
    url: 'menu/update',
    method: 'post',
    data: menu
  })
}

const getTree = () => {
  return proxy({
    url: 'menu/tree',
    method: 'get'
  })
}

const getChildren = (code) => {
  return proxy({
    url: 'menu/children',
    method: 'get',
    params: {
      code: code
    }
  })
}

const getOptions = () => {
  return proxy({
    url: 'menu/options',
    method: 'get'
  })
}

export default { create, update, getTree, getChildren, getOptions }



