import proxy from './proxy'

const create = (data) => {
  return proxy({
    url: '/client/create',
    data: data,
    method: 'post'
  })
}

const getPage = (params) => {
  return proxy({
    url: '/client/page',
    params: params
  })
}

export default { create, getPage }



