<template>
  <el-row>
    <el-col :span="4">
      <Menu-tree 
        :data="treeData"
        @current-node-change="currentNodeChange">
      </Menu-tree>
    </el-col>
    <el-col :span="20" class="permisson-content-table">
      <el-button-group class="button-toolbar">
        <el-button :plain="true" type="success" @click="prepareFor('add')">
          <i class="fa fa-plus" aria-hidden="true"></i> 新增
        </el-button>
        <el-button :plain="true" type="primary" @click="prepareFor('edit')">
          <i class="fa fa-pencil" aria-hidden="true"></i> 编辑
        </el-button>
        <el-button :plain="true" type="danger" @click="prepareFor('remove')">
          <i class="fa fa-times" aria-hidden="true"></i> 删除
        </el-button>
      </el-button-group>
      <Permission-table 
        ref="permissionTable"
        class="content-table"
        :data="gridData"
        @selection-change="handleSelectionChange">
      </Permission-table>
      <el-dialog title="权限" :visible.sync="form.visible" size="tiny">
        <el-form :model="formData" label-width="80px">
          <el-form-item label="编码">
            <el-input v-model="formData.code" :disabled="stage == 'edit'" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="名称">
            <el-input v-model="formData.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="描述">
            <el-input type="textarea" v-model="formData.remark" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="form.visible = false">取 消</el-button>
          <el-button type="primary" @click="submit(stage)">确 定</el-button>
        </div>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>

import $ from '@/apis'
import MenuTree from '@/views/menu/MenuTree'
import PermissionTable from '@/views/permission/PermissionTable'

export default {
  components: {
    MenuTree,
    PermissionTable
  },
  data () {
    return {
      stage: 'list',
      form: {
        visible: false,
        data: {}
      },
      tree: {
        loading: false,
        data: []
      },
      grid: {
        loading: false,
        data: [],
        selections: []
      }
    }
  },
  created () {
    this.loadTreeData()
    // this.loadGridData()
  },
  computed: {
    formData: {
      get () {
        return this.form.data
      }
    },
    treeData: {
      get () {
        return this.tree.data
      }
    },
    gridData: {
      get () {
        return this.grid.data
      }
    }
  },
  methods: {
    prepareFor (stage = 'add') {
      if (stage === 'add') {
        this.form.visible = true
      }
      if (stage === 'edit') {
        this.form.visible = true
        this.form.data = this.grid.selections[0]
      }
      this.stage = stage
    },
    loadTreeData () {
      this.tree.loading = true
      $.menu.getTree(this.query)
      .then(data => {
        this.tree.loading = false
        this.tree.data = data
      })
      .catch(e => {
        this.tree.loading = false
        console.log(e)
      })
    },
    loadGridData (code) {
      this.grid.loading = true
      $.permission.getContent(code)
      .then(data => {
        this.grid.loading = false
        this.grid.data = data
      })
      .catch(e => {
        this.grid.loading = false
        console.error(e)
      })
    },
    submit (stage = 'add') {
      $.permission.update(this.form.data)
      .then(data => {
        this.form.data = {}
        this.form.visible = false
      })
      .catch(e => {
        console.error(e)
      })
    },
    handleSelectionChange (val) {
      this.grid.selections = val
    },
    currentNodeChange (newNode, oldNode) {
      this.loadGridData(newNode.data.code)
    }
  }
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.permisson-content-table {
  padding-left: 10px;
  .content-table {
    margin-top: 10px; 
  }
}
</style>



