import proxy from './proxy'
import user from './user'
import menu from './menu'
import permission from './permission'
import client from './client'

const login = (username, password) => {
  const result = proxy.post('/login', {
    username: username,
    password: password
  })
  return result
}

const logout = () => {
  return proxy({
    url: '/logout',
    method: 'get'
  })
}

export default { login, logout, user, menu, permission, client }



