<template>
  <el-row class="main-container">
    <el-button-group class="button-toolbar">
      <el-button :plain="true" type="success" @click="prepareFor('add')">
        <i class="fa fa-plus" aria-hidden="true"></i> 新增
      </el-button>
      <el-button :plain="true" type="primary" @click="prepareFor('edit')">
        <i class="fa fa-pencil" aria-hidden="true"></i> 编辑
      </el-button>
      <el-button :plain="true" type="danger" @click="prepareFor('remove')">
        <i class="fa fa-times" aria-hidden="true"></i> 删除
      </el-button>
    </el-button-group>
    <el-table
      stripe
      class="main-content"
      highlight-current-row
      :data="gridData"
      :v-loading="grid.loading"
      element-loading-text="数据加载中"
      @selection-change="handleSelectionChange"
      style="width: 100%">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="80"></el-table-column>
      <el-table-column prop="name" label="应用名称" width="200"></el-table-column>
      <el-table-column prop="clientId" label="App Id" width="200"></el-table-column>
      <el-table-column prop="clientSecret" label="App Secret"></el-table-column>
      <el-table-column prop="redirectUri" label="应用地址" width="200"></el-table-column>
    </el-table>
    <div class="block">
      <el-pagination 
        layout="prev, pager, next, jumper, ->, total" 
        :page-size="grid.query.size"
        :total="grid.query.total"
        @current-change="loadGridData">
      </el-pagination>
    </div>
    <!-- 菜单表单 -->
    <el-dialog title="新增菜单" :visible.sync="form.visible" size="tiny">
      <el-form :model="formData" label-width="120px">
        <el-form-item label="App Id">
          <el-input v-model="formData.clientId" :disabled="true" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="App Secret">
          <el-input v-model="formData.clientSecret" :disabled="true" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="应用名称">
          <el-input v-model="formData.name"></el-input>
        </el-form-item>
        <el-form-item label="应用地址">
          <el-input v-model="formData.redirectUri"></el-input>
        </el-form-item>
        <el-form-item label="应用简介">
          <el-input v-model="formData.description" type="textarea"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="form.visible = false">取 消</el-button>
        <el-button type="primary" @click="submit(stage)">确 定</el-button>
      </div>
    </el-dialog>
  </el-row>
</template>

<script>
import $ from '@/apis'

export default {
  data () {
    return {
      stage: 'list',
      form: {
        visible: false,
        data: {}
      },
      grid: {
        loading: false,
        data: [],
        query: {
          desc: '',
          asc: 'createdTime',
          page: 1,
          size: 15,
          total: null,
          bundle: {}
        }
      }
    }
  },
  computed: {
    formData: {
      get () {
        return this.form.data
      }
    },
    gridData: {
      get () {
        return this.grid.data
      }
    }
  },
  created () {
    this.loadGridData()
  },
  methods: {
    prepareFor (stage = 'add') {
      if (stage === 'add') {
        this.form.visible = true
      }
    },
    handleSelectionChange (val) {
      console.log(val)
    },
    loadGridData () {
      this.grid.loading = true
      $.client.getPage()
      .then(data => {
        this.grid.data = data.content
        this.grid.query.page = data.page
        this.grid.query.size = data.size
        this.grid.query.total = data.total
      })
      .catch(e => {
        this.grid.loading = false
        console.log(e)
      })
    },
    submit (stage) {
      $.client.create(this.form.data)
      .then(data => {
        this.form.visible = false
      })
      .catch(e => {
        this.form.visible = false
        this.$message({
          type: 'error',
          message: e.message
        })
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.main-container {
  .main-content {
    margin-top: 10px;
  }
}
</style>



