<template>
  <el-table
    stripe
    highlight-current-row
    :data="data"
    ref="permissionTable"
    :v-loading="loading"
    element-loading-text="数据加载中"
    @row-click="handleRowClick"
    @selection-change="handleSelectionChange"
    style="width: 100%">
    <el-table-column type="selection" width="55"></el-table-column>
    <el-table-column type="index" width="80"></el-table-column>
    <el-table-column prop="code" label="编码" width="200"></el-table-column>
    <el-table-column prop="name" label="名称" width="200"></el-table-column>
    <el-table-column prop="remark" label="描述"></el-table-column>
    <el-table-column prop="createdTime" label="创建时间" width="200"></el-table-column>
  </el-table>
</template>
<script>

export default {
  props: {
    data: {
      type: Array
    },
    loading: {
      type: Boolean
    }
  },
  methods: {
    handleSelectionChange (val) {
      this.$emit('selection-change', val)
    },
    handleRowClick (row, event, column) {
      this.$refs.permissionTable.clearSelection()
      this.$refs.permissionTable.toggleRowSelection(row)
    }
  }
}
</script>

<style>

</style>



