import proxy from './proxy'

const getList = (params) => {
  return proxy({
    url: '/permission/list',
    method: 'get',
    params
  })
}

const getContent = (code) => {
  return proxy({
    url: '/permission/content',
    method: 'get',
    params: {
      code: code
    }
  })
}

const create = (user) => {
  return proxy({
    url: '/permission/create',
    method: 'post',
    data: user
  })
}

const remove = (names) => {
  return proxy({
    url: '/permission/delete',
    method: 'post',
    data: names
  })
}

const update = (user) => {
  return proxy({
    url: '/permission/update',
    method: 'post',
    data: user
  })
}

export default { getContent, getList, create, remove, update }



