<template>
  <el-row class="main-container">
    <el-button-group class="button-toolbar">
      <el-button :plain="true" type="success" @click="prepareFor('add')">
        <i class="fa fa-plus" aria-hidden="true"></i> 新增
      </el-button>
      <el-button :plain="true" type="primary" @click="prepareFor('edit')">
        <i class="fa fa-pencil" aria-hidden="true"></i> 编辑
      </el-button>
      <el-button :plain="true" type="danger" @click="prepareFor('remove')">
        <i class="fa fa-times" aria-hidden="true"></i> 删除
      </el-button>
    </el-button-group>
    <el-table
      stripe
      class="main-content"
      highlight-current-row
      :data="gridData"
      :v-loading="grid.loading"
      element-loading-text="数据加载中"
      @selection-change="handleSelectionChange"
      style="width: 100%">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="80"></el-table-column>
      <el-table-column prop="name" label="角色名称"></el-table-column>
      <el-table-column prop="code" label="角色编码"></el-table-column>
    </el-table>
    <div class="block">
      <el-pagination 
        layout="prev, pager, next, jumper, ->, total" 
        :page-size="grid.query.size"
        :total="grid.query.total"
        @current-change="loadGridData">
      </el-pagination>
    </div>
    <!-- 菜单表单 -->
    <el-dialog title="新增菜单" :visible.sync="form.visible" size="tiny">
      <el-form :model="formData" label-width="120px">
        <el-form-item label="角色名称">
          <el-input v-model="formData.code" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="角色编码">
          <el-input v-model="formData.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="权限分配">
          <el-checkbox-group v-model="checkList">
            <el-checkbox label="复选框 A"></el-checkbox>
            <el-checkbox label="复选框 B"></el-checkbox>
            <el-checkbox label="复选框 C"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-table
        :data="permissionData">
          <el-table-column
            label="日期">
            <template scope="scope">
              <el-icon name="time"></el-icon>
              <span style="margin-left: 10px">{{ scope.row.date }}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="日期">
            <template scope="scope">
              <el-icon name="time"></el-icon>
              <span style="margin-left: 10px">{{ scope.row.date }}</span>
            </template>
          </el-table-column>
          <el-table-column
            label="日期">
            <template scope="scope">
              <el-icon name="time"></el-icon>
              <span style="margin-left: 10px">{{ scope.row.date }}</span>
            </template>
          </el-table-column>
        </el-table>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="form.visible = false">取 消</el-button>
        <el-button type="primary" @click="submit(stage)">确 定</el-button>
      </div>
    </el-dialog>
  </el-row>
</template>

<script>
export default {
  data () {
    return {
      grid: {
        data: [],
        query: {}
      },
      form: {
        visible: false,
        data: {}
      },
      permissionData: [{
        date: new Date()
      }]
    }
  },
  computed: {
    formData: {
      get () {
        return this.form.data
      }
    },
    gridData: {
      get () {
        return this.grid.data
      }
    }
  },
  methods: {
    prepareFor (stage = 'add') {
      if (stage === 'add') {
        this.form.visible = true
      }
    }
  }
}
</script>

<style>
  .main-content {
    margin-top: 10px;
  }
</style>



